<template>
  <div>
    <el-space direction="vertical" :size="20">
      <h1>帖子管理</h1>
      <!-- 搜索框和按钮 -->
      <el-input v-model="searchQuery" placeholder="请输入帖子标题" style="width: 300px;">
        <template #append>
          <el-button type="primary" @click="onSearch">搜索</el-button>
        </template>
      </el-input>
      <el-table :data="filteredPosts" style="width: 100%">
        <el-table-column label="标题">
          <template #default="scope">
            <a :href="$http.server_host + '/post/detail/' + scope.row.id" target="_blank">{{
                scope.row.title
              }}</a>
          </template>
        </el-table-column>
        <el-table-column prop="create_time" label="发布时间" width="180"/>
        <el-table-column prop="board.name" label="所属板块"/>
        <el-table-column prop="author.username" label="作者"/>
        <el-table-column label="操作">
          <template #default="scope">
            <el-button
                type="danger"
                circle
                size="mini"
                @click="onDeletePostClick(scope.$index)"
            >
              <el-icon>
                <delete/>
              </el-icon>
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <div style="text-align: center;">
        <el-pagination background layout="prev, pager, next" :total="total_count" :current-page="page"
                       @current-change="onPageChange"/>
      </div>
    </el-space>

    <!-- 删除确认对话框 -->
    <el-dialog
        v-model="confirmDialogVisible"
        title="提示"
        width="30%"
    >
      <span>如果删除帖子，该帖子下所有的评论也会被删除，您确定要删除吗？</span>
      <template #footer>
      <span class="dialog-footer">
        <el-button @click="confirmDialogVisible = false">取消</el-button>
        <el-button type="primary" @click="onConfirmDeletePostClick">确定</el-button>
      </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import {Delete} from "@element-plus/icons";
import {ElMessage} from "element-plus";

export default {
  name: "Post_Page",
  data() {
    return {
      deletingIndex: 0,
      confirmDialogVisible: false,
      posts: [],
      total_count: 0,
      page: 1,
      searchQuery: ''  // 新增搜索查询字符串
    };
  },
  mounted() {
    this.getPostList(1);
  },
  computed: {
    // 新增计算属性来过滤帖子
    filteredPosts() {
      if (this.searchQuery) {
        return this.posts.filter(post =>
          post.title.toLowerCase().includes(this.searchQuery.toLowerCase())
        );
      }
      return this.posts;
    }
  },
  methods: {
    getPostList(page) {
      this.$http.getPostList(page).then(response => {
        if (response["code"] === 200) {
          let data = response["data"];
          this.posts = data["post_list"];
          this.total_count = data["total_count"];
          this.page = data["page"];
        }
      });
    },
    onDeletePostClick(index) {
      this.confirmDialogVisible = true;
      this.deletingIndex = index;
    },
    onConfirmDeletePostClick() {
      let post = this.posts[this.deletingIndex];
      this.$http.deletePost(post.id).then(response => {
        if (response["code"] === 200) {
          this.posts.splice(this.deletingIndex, 1);
          this.confirmDialogVisible = false;
          ElMessage.success("删除成功");
        } else {
          ElMessage.info(response["message"]);
        }
      });
    },
    onPageChange(current_page) {
      this.page = current_page;
      this.getPostList(current_page);
    },
    onSearch() {  // 新增搜索方法
      this.getPostList(1);  // 重新获取第一页数据
    }
  },
  components: {
    Delete
  }
};
</script>

<style scoped>
.el-space {
  display: block;
}
</style>
