<template>
  <h1>板块管理</h1>
  <div style="text-align: right">
    <el-button type="primary" @click="onAddBannerClick">
      <el-icon>
        <plus/>
      </el-icon>
      添加板块
    </el-button>
    <el-table :data="sortedBoards" style="width: 100%">
      <el-table-column prop="name" label="名称" width="300px"/>
      <el-table-column prop="create_time" label="创建时间" width="300px"/>
      <el-table-column prop="priority" label="优先级" width="300px"/>
      <el-table-column label="操作" width="300px">
        <template #default="scope">
          <el-button type="primary" circle @click="onEditEvent(scope.row)">
            <el-icon>
              <Edit/>
            </el-icon>
          </el-button>
          <el-button type="danger" circle @click="onDeleteEvent(scope.row)">
            <el-icon>
              <Delete/>
            </el-icon>
          </el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
  <div style="text-align: center;">
    <el-pagination background layout="prev, pager, next" :total="total_count" :current-page="page"
                   @current-change="onPageChange"/>
  </div>
  <el-dialog
      v-model="bannerDialogVisible"
      title="添加/修改板块"
      width="30%"
  >
    <el-form :model="form" :rules="rules" ref="dialogForm">
      <el-form-item label="名称" prop="name">
        <el-input v-model="form.name" autocomplete="off"/>
      </el-form-item>
      <el-form-item label="优先级" prop="priority">
        <el-input v-model="form.priority" autocomplete="off" type="number"/>
      </el-form-item>
    </el-form>
    <template #footer>
      <div class="dialog-footer">
        <el-button @click="bannerDialogVisible = false">取消</el-button>
        <el-button type="primary" @click="onDialogSubmitEvent">
          确认
        </el-button>
      </div>
    </template>
  </el-dialog>
</template>

<script>
import { Delete, Edit, Plus } from "@element-plus/icons";
import { ElMessage, ElMessageBox } from "element-plus";

export default {
  name: "Board_Page",
  data() {
    return {
      deletingBoardId: 0,
      bannerDialogVisible: false,
      boards: [],
      total_count: 0,
      page: 1,
      form: {
        id: null,
        name: "",
        priority: 0
      },
      rules: {
        name: [
          { required: true, message: '请输入名称', trigger: 'blur' },
        ],
        priority: [
          { required: true, message: '请输入优先级', trigger: 'blur' },
          { validator: this.validatePriority, trigger: 'blur' }
        ]
      }
    };
  },
  mounted() {
    this.fetchBoardList();
  },
  computed: {
    sortedBoards() {
      return this.boards.slice().sort((a, b) => b.priority - a.priority);
    }
  },
  methods: {
    fetchBoardList() {
      this.$http.getBoardList({ page: this.page }).then(res => {
        if (res['code'] == 200) {
          this.boards = res['data']["board_list"];
          this.total_count = res['data']["total_count"];
          this.page = res['data']["page"];
        } else {
          ElMessage.error(res['message']);
        }
      })
    },
    onAddBannerClick() {
      this.initForm();
      this.bannerDialogVisible = true;
      // 验证优先级是否重复
      this.$refs.dialogForm.validateField('priority', (valid) => {
        if (valid) {
          this.bannerDialogVisible = true;
        } else {
          ElMessage.error("优先级不能重复");
        }
      });
    },
    onEditEvent(board) {
      this.initForm(board);
      this.bannerDialogVisible = true;
    },
    onDeleteEvent(board) {
      this.deletingBoardId = board.id;
      ElMessageBox.confirm('相关板块内容都会被删除，确定要删除吗？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$http.deleteBoard(this.deletingBoardId).then(res => {
          if (res['code'] == 200) {
            this.boards = this.boards.filter(board => board.id !== this.deletingBoardId);
            ElMessage.success("删除成功");
          } else {
            ElMessage.error(res['message']);
          }
        });
      }).catch(() => {
        ElMessage.info("已取消删除");
      });
    },
    onPageChange(page) {
      this.page = page;
      this.fetchBoardList();
    },
    initForm(board) {
      if (board) {
        this.form = {
          id: board.id,
          name: board.name,
          priority: board.priority
        };
      } else {
        this.form = {
          id: null,
          name: "",
          priority: 0
        };
      }
    },
    onDialogSubmitEvent() {
      this.$refs.dialogForm.validate((valid) => {
        if (!valid) {
          ElMessage.error("请确保数据输入正确");
          return;
        }
        if (this.form.id) {
          // 修改现有板块
          this.$http.updateBoard(this.form).then(res => {
            if (res['code'] == 200) {
              this.bannerDialogVisible = false;
              // 重新获取列表以确保数据更新
              this.fetchBoardList();
              ElMessage.success("修改成功");
            } else {
              ElMessage.error(res['message']);
            }
          });
        } else {
          // 添加新板块
          this.$http.addBoard(this.form).then(res => {
            if (res['code'] == 200) {
              this.bannerDialogVisible = false;
              // 重新获取列表以确保数据更新
              this.fetchBoardList();
              ElMessage.success("添加成功");
            } else {
              ElMessage.error(res['message']);
            }
          });
        }
      });
    },
    validatePriority(rule, value, callback) {
      if (this.boards.some(board => board.priority === value && board.id !== this.form.id)) {
        callback(new Error('优先级不能重复'));
      } else {
        callback();
      }
    }
  },
  components: {
    Edit, Delete, Plus
  }
};
</script>

<style scoped>
.el-table-column {
  padding: 0 10px; /* 调整列的间隔 */
}
</style>
