<template>
  <div>
    <el-space direction="vertical" :size="20">
      <h1>用户管理</h1>
      <el-table :data="users" style="width: 100%">
        <el-table-column prop="username" label="用户名"/>
        <el-table-column prop="email" label="邮箱"/>
        <el-table-column prop="join_time" label="加入时间"/>
        <el-table-column label="状态">
          <template #default="scope">
            <span v-if="scope.row.role_id === 3">开发者</span>
            <span v-if="scope.row.role_id === 2">管理员</span>
            <span v-if="scope.row.role_id === 1">运营</span>
          </template>
        </el-table-column>
        <el-table-column label="操作">
          <template #default="scope">
            <el-button
                type="primary"
                circle
                size="mini"
                @click="onActiveUserClick(scope.$index)"
            >
              <el-icon>
                <Edit />
              </el-icon>
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <div style="text-align: center;">
        <el-pagination background layout="prev, pager, next" :total="total_count" :current-page="page"
                       @current-change="onPageChange"/>
      </div>
    </el-space>

    <!-- 删除轮播图确认对话框 -->
    <el-dialog
        v-model="confirmDialogVisible"
        title="提示"
        width="30%"
    >
      <span>{{ message }}</span>
      <template #footer>
      <span class="dialog-footer">
        <el-button type="primary"  @click="confirmDialogVisible = false">取消</el-button>
        <el-button type="primary" @click="ChangeCreatorClick">开发者</el-button>
        <el-button type="primary" @click="ChangeManagerClick">管理者</el-button>
        <el-button type="primary" @click="ChangeOperatorClick">运营</el-button>
      </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import {Edit} from "@element-plus/icons";
import {ElMessage} from "element-plus";

export default {
  name: "User_Page",
  data() {
    return {
      confirmDialogVisible: false,
      users: [],
      activingIndex: 0,
      message: "",
      total_count: 0,
      page: 1,

    }
  },
  mounted() {
    this.getUserList(1);
  },
  methods: {
    getUserList(page) {
      this.$http.getStaffList(page).then(res => {
        if (res && res['code'] == 200) {
          this.users = res['data']['user_list']
        this.total_count = res['data']['total_count']
        this.page = res['data']['page']
        } else {
          ElMessage.info("获取用户列表失败！");
        }
      });
    },
    onPageChange(page) {
      this.getUserList(page);
    },
    onActiveUserClick(index) {
      this.activingIndex = index;
      console.log(index);
      this.confirmDialogVisible = true;
      this.message = "请选择要激活的权限";
    },
    ChangeCreatorClick() {
      let user = this.users[this.activingIndex];
      if (user.role_id == 3) {
        ElMessage.info("该用户已经是开发者！");
        this.confirmDialogVisible = false;
        return;
      }
      this.$http.changeRole(user.id, 3).then(res => {
         if (res && res['code'] == 200) {
          ElMessage.success("操作成功！");
          this.confirmDialogVisible = false;
          let user = res.data;
          this.users.splice(this.activingIndex, 1, user);
        } else {
          ElMessage.info("操作失败！");
          this.confirmDialogVisible = false;
        }
      });
    },
    ChangeManagerClick() {
      let user = this.users[this.activingIndex];
      if (user.role_id == 2) {
        ElMessage.info("该用户已经是管理者！");
        this.confirmDialogVisible = false;
        return;
      }
      this.$http.changeRole(user.id, 2).then(res => {
        if (res && res['code'] == 200) {
          ElMessage.success("操作成功！");
          this.confirmDialogVisible = false;
          let user = res.data;
          this.users.splice(this.activingIndex, 1, user);
        } else {
          ElMessage.info("操作失败！");
          this.confirmDialogVisible = false;
        }
      });
    },
    ChangeOperatorClick() {
      let user = this.users[this.activingIndex];
      if (user.role_id == 1) {
        ElMessage.info("该用户已经是运营！");
        this.confirmDialogVisible = false;
        return;
      }
      this.$http.changeRole(user.id, 1).then(res => {
        if (res && res['code'] == 200) {
          ElMessage.success("操作成功！");
          this.confirmDialogVisible = false;
          let user = res.data;
          this.users.splice(this.activingIndex, 1, user);
        } else {
          ElMessage.info("操作失败！");
          this.confirmDialogVisible = false;
        }
      });
    },
  },
  components: {
      Edit
    }
}
</script>

<style scoped>
.el-space {
  display: block;
}
</style>