import { createRouter, createWebHistory } from 'vue-router'
import Home_Page from '@/components/home.vue'
import Banner_Page from '@/components/banner.vue'
import Post_Page from '@/components/post.vue'
import Comment_Page from '@/components/commnet.vue'
import User_Page from '@/components/user.vue'
import Manager_Page from '@/components/manager.vue'
import Board_Page from '@/components/board.vue'
const routes = [{
    path: '/', component: Home_Page, name: "home"
  },{
    path: '/banner', component: Banner_Page, name: "banner"
  },{
    path: '/user', component: User_Page, name: "user"
  },{ 
    path: '/post', component: Post_Page, name: "post" 
  },{ 
    path: '/commnet', component: Comment_Page, name: "comment"
},{
    path:'/manger' ,component: Manager_Page,name:'manager'
},{
    path:'/board',component:Board_Page,name:'board'
}]

const router = createRouter({
  history: createWebHistory(),
  routes
})

export default router