<template>
  <div>
    <el-space direction="vertical" :size="20">
      <h1>用户管理</h1>
      <el-table :data="users" style="width: 100%">
        <el-table-column prop="username" label="用户名"/>
        <el-table-column prop="email" label="邮箱"/>
        <el-table-column prop="join_time" label="加入时间"/>
        <el-table-column label="员工">
          <template #default="scope">
            <el-tag v-if="scope.row.is_staff">是</el-tag>
            <el-tag v-else type="danger">否</el-tag>
          </template>
        </el-table-column>
        <el-table-column label="状态">
          <template #default="scope">
            <el-tag v-if="scope.row.is_active" type="success">正常</el-tag>
            <el-tag v-else type="danger">拉黑</el-tag>
          </template>
        </el-table-column>
        <el-table-column label="操作">
          <template #default="scope">
            <el-button
                type="danger"
                circle
                size="mini"
                @click="onActiveUserClick(scope.$index)"
            >
              <el-icon>
                <delete/>
              </el-icon>
            </el-button>
            <el-button
                type="primary"
                circle
                size="mini"
                @click="onStaffClick(scope.$index)"
            >
              <el-icon>
                <edit/>
              </el-icon>
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <div style="text-align: center;">
        <el-pagination background layout="prev, pager, next" :total="total_count" :current-page="page"
                       @current-change="onPageChange"/>
      </div>
    </el-space>

    <!-- 删除轮播图确认对话框 -->
    <el-dialog
        v-model="confirmDialogVisible"
        title="提示"
        width="30%"
    >
      <span>{{ message }}</span>
      <template #footer>
      <span class="dialog-footer">
        <el-button @click="confirmDialogVisible = false">取消</el-button>
        <el-button type="primary" @click="onConfirmActiveUserClick">确定</el-button>
      </span>
      </template>
    </el-dialog>
    <!-- 设置员工确认对话框 -->
  <el-dialog
    v-model="confirmStaffVisible"
    title="提示"
    width="30%"
>
  <span>{{ message }}</span>
  <template #footer>
    <span class="dialog-footer">
      <el-button @click="confirmStaffVisible = false">取消</el-button>
      <el-button type="primary" @click="onConfirmStaffClick">确定</el-button>
    </span>
  </template>
</el-dialog>
  </div>
</template>

<script>
import {Delete, Edit} from "@element-plus/icons";
import {ElMessage} from 'element-plus';

export default {
  name: "User_Page",
  data() {
    return {
      confirmDialogVisible: false,
      confirmStaffVisible: false,
      users: [],
      activingIndex: 0,
      staffingIndex: 0,
      message: "",
      total_count: 0,
      page: 1,
    }
  },
  mounted() {
    this.getUserList(1);
  },
  methods: {
    onPageChange(page) {
      this.getUserList(page)
    },
    getUserList(page) {
      this.$http.getUserList(page).then(res => {
        this.users = res['data']['user_list']
        this.total_count = res['data']['total_count']
        this.page = res['data']['page']
      });
    },
    onStaffClick(index) {
      this.staffingIndex = index;
      if(this.users[index].role_id >this.$auth.user.role_id){
        ElMessage.info("权限不足！");
        return;
      }
      this.confirmStaffVisible = true;
      const user = this.users[index];
      if (user.is_staff) {
        this.message = "您确定要开除此用户吗？"
      } else {
        this.message = "您确定要将该用户设置为员工吗？"
      }
    },
    onActiveUserClick(index) {
      this.activingIndex = index;
      if(this.users[index].role_id >this.$auth.user.role_id){
        ElMessage.info("权限不足！");
        return;
      }
      this.confirmDialogVisible = true;
      const user = this.users[index];
      if (user.is_active) {
        this.message = "您确定要拉黑此用户吗？"
      } else {
        this.message = "您确定要解除拉黑此用户吗？"
      }
    },
    onConfirmStaffClick() {
      console.log("test");
      let user = this.users[this.staffingIndex];
      let is_staff = user.is_staff ? 0 : 1;
      this.$http.staffUser(user.id, is_staff).then(res => {
        if (res && res['code'] == 200) {
          ElMessage.success("操作成功！");
          this.confirmStaffVisible = false;
          let user = res.data;
          this.users.splice(this.staffingIndex, 1, user);
        } else {
          ElMessage.info("操作失败！");
          this.confirmDialogVisible = false;
        }
      })
    },

    onConfirmActiveUserClick() {
      let user = this.users[this.activingIndex];
      let is_active = user.is_active ? 0 : 1;
      this.$http.activeUser(user.id, is_active).then(res => {
        if (res && res['code'] == 200) {
          ElMessage.success("操作成功！");
          this.confirmDialogVisible = false;
          let user = res.data;
          this.users.splice(this.activingIndex, 1, user);
        } else {
          ElMessage.info("操作失败！");
          this.confirmDialogVisible = false;
        }
      })
    },
  },
  components: {
    Delete,
    Edit
  }
}
</script>

<style scoped>
.el-space {
  display: block;
}
</style>