<template>
  <div id="home">
    <h1>Home</h1>
    <div id="board-post-count" class="chart"></div>
    <div id="day7-post-count" class="chart"></div>
  </div>
</template>

<script>
import * as echarts from 'echarts';
import {ElMessage} from "element-plus";

export default {
  name: 'Home_Page',
  mounted() {
    this.loadBoardPostCountchart();
    this.load7DayPostCountchart();
  },
  methods: {
    loadBoardPostCountchart() {
      this.$http.getBoardPostCount().then(response => {
        if (response["code"] !== 200) {
          ElMessage.error(response['message']);
          return;
        }
        var data = response['data']
        var board_names = data['board_name']
        var post_counts = data['post_count']
        var chartDom = document.getElementById('board-post-count');
        var myChart = echarts.init(chartDom);
        var option;
        option = {
          title:{
            text: '板块贴子数',x:"center",y:"top",
          },
           tooltip:{
            trigger: 'axis',
          },
          xAxis: {
            type: 'category',
            data: board_names
          },
          yAxis: {
            type: 'value'
          },
          series: [
            {
              data: post_counts,
              type: 'bar'
            }
          ]
        };

        option && myChart.setOption(option);
      })
    },
    load7DayPostCountchart() {
      this.$http.getDay7PostCount().then(response => {
        if (response["code"] != 200) {
          ElMessage.error(response['message'])
          return;
        }
        var data = response['data']
        var dates = data['dates']
        var counts = data['counts']
        var chartDom = document.getElementById('day7-post-count');
        var myChart = echarts.init(chartDom);
        var option;

        option = {
           title:{
            text: '近七天贴子数',x:"center",y:"top",
          },
          tooltip:{
            trigger: 'axis',
          },
          xAxis: {
            type: 'category',
            boundaryGap: false,
            data: dates
          },
          yAxis: {
            type: 'value'
          },
          series: [
            {
              data: counts,
              type: 'line',
              areaStyle: {}
            }
          ]
        };

        option && myChart.setOption(option);
      })
    }
  }
}
</script>

<style scoped>
.chart {
  height: 300px;
  width: 100%;
}

</style>