<template>
  <div>
    <el-space direction="vertical" :size="20">
      <h1>评论管理</h1>
      <!-- 搜索框和按钮 -->
      <el-input v-model="searchQuery" placeholder="请输入评论内容" style="width: 300px;">
        <template #append>
          <el-button type="primary" @click="onSearch">搜索</el-button>
        </template>
      </el-input>
      <el-table :data="filteredComments" style="width: 100%">
        <el-table-column prop="content" label="内容" />
        <el-table-column prop="author.username" label="作者" />
        <el-table-column label="帖子">
          <template #default="scope">
            <a :href="$http.server_host + '/post/detail/' + scope.row.post.id" target="_blank">{{ scope.row.post.title }}</a>
          </template>
        </el-table-column>
        <el-table-column prop="create_time" label="发布时间" width="180" />
        <el-table-column label="操作">
          <template #default="scope">
            <el-button
              type="danger"
              circle
              size="mini"
              @click="onDeleteCommentClick(scope.$index)"
            >
              <el-icon><delete /></el-icon>
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <div style="text-align: center;">
        <el-pagination background layout="prev, pager, next" :total="total_count" :current-page="page"
                       @current-change="onPageChange"/>
      </div>
    </el-space>

    <!-- 删除确认对话框 -->
    <el-dialog v-model="confirmDialogVisible" title="提示" width="30%">
      <span>您确定要删除这个评论吗？</span>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="confirmDialogVisible = false">取消</el-button>
          <el-button type="primary" @click="onConfirmDeleteCommentClick">确定</el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import { ElMessage } from 'element-plus';
import {Delete} from "@element-plus/icons";

export default {
  name: "Comment_Page",
  data() {
    return {
      deletingIndex: 0,
      confirmDialogVisible: false,
      comments: [],
      total_count: 0,
      page: 1,
      searchQuery: ''  // 新增搜索查询字符串
    };
  },
  mounted() {
    this.getCommentList(1);
  },
  computed: {
    // 新增计算属性来过滤评论
    filteredComments() {
      if (this.searchQuery) {
        return this.comments.filter(comment =>
          comment.content.toLowerCase().includes(this.searchQuery.toLowerCase())
        );
      }
      return this.comments;
    }
  },
  methods: {
    getCommentList(page) {
      this.$http.getCommentList(page).then(res => {
        if (res && res['code'] === 200) {
          let data = res['data'];
          this.comments = data['comment_list'];
          this.total_count = data['total_count'];
          this.page = data['page'];
        }
      });
    },
    onDeleteCommentClick(index) {
      this.deletingIndex = index;
      this.confirmDialogVisible = true;
    },
    onConfirmDeleteCommentClick() {
      let comment = this.comments[this.deletingIndex];
      this.$http.deleteComment(comment.id).then(res => {
        if (res && res['code'] === 200) {
          ElMessage.success("评论删除成功！");
          this.confirmDialogVisible = false;
          this.comments.splice(this.deletingIndex, 1);
        } else {
          ElMessage.info(res['message']);
        }
      });
    },
    onPageChange(page) {
      this.page = page;
      this.getCommentList(page);
    },
    onSearch() {  // 新增搜索方法
      this.page = 1;  // 搜索时重置页码为1
      this.getCommentList(1);  // 重新获取第一页数据
    }
  },
  components: {
    Delete
  }
};
</script>

<style scoped>
.el-space {
  display: block;
}
</style>
